import { lazy } from "react";
import { route } from "../shared/constants/AllRoutes";

// MAIN PAGE
const MainPage = lazy(() => import('../views/mainPage/index'))

// WORK PAGE
const WorkPage = lazy(() => import('../views/workPage/index'))
const DetailWorkPage = lazy(() => import('../views/workPage/detail/index'))

const RoutesDetails = [
    // MAIN PAGE
    { path: route.mainPage, Component: MainPage, exact: true },

    // WORK PAGE
    { path: route.work, Component: WorkPage, exact: true },
    { path: route.viewWork(':id'), Component: DetailWorkPage, exact: true },
    // { path: route.mainPage, Component: MainPage, exact: true },
]

export default RoutesDetails